import React, { Component } from 'react';
import { Row, Col, Menu, Icon, Form } from 'antd';
import { BtnAddNew, BtnDelete, BtnEdit } from 'components/common/button';
import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class MenuTabTop extends Component {
  state = {};

  handleClick = e => {
    // console.log('click ', e);
    this.setState({
      current: e.key,
    });
    if (e.key === 'enteringInfo') {
      console.log('render');
    }
  };
  render() {
    return (
      <Form onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={24}>
            <Menu onClick={this.handleClick} selectedKeys={[this.state.current]} mode="horizontal">
              <Menu.Item key="add">
                <BtnAddNew />
              </Menu.Item>
              <Menu.Item key="edit">
                <BtnEdit />
              </Menu.Item>
              <Menu.Item key="delete">
                <BtnDelete />
              </Menu.Item>
              <Menu.Item key="print">
                <Icon type="printer" theme="twoTone" twoToneColor="#6699ff" />
                In danh sách
              </Menu.Item>
            </Menu>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default MenuTabTop;



