import React, { Component } from 'react';
import { Row, Col, Form, Select } from 'antd';
import { setLectureHallList, setTypeRooms } from 'dump/data';
import './style.scss';

@Form.create({ name: 'formClassList' })
class FormInfoCommon extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    return (
      <Form onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="Giảng đường:">
              {getFieldDecorator('lectureHall', {})(
                <Select>
                  {setLectureHallList.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Loại phòng:">
              {getFieldDecorator('typeRooms', {})(
                <Select>
                  {setTypeRooms.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label=" ">
              {getFieldDecorator('typeRooms', {})(
                <div>
                  Tổng số SV: <b>1</b>
                </div>
              )}
            </Form.Item>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoCommon;



