import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import { BtnAddNew, BtnDelete, BtnSearch } from 'components/common/button';
import MenuTabTop from './MenuTabTop';
import FormInfoCommon from './FormInfoCommon';
import TableRoomsForAssignSubject from './TableRoomsForAssignSubject';
import TableSubjectNameNotSelected from './TableSubjectNameNotSelected';
import TableSubjectNameSelected from './TableSubjectNameSelected';
import './style.scss';

class ClassListPage extends Component {
  state = {};
  render() {
    return (
      <Card title="Danh sách phòng học - Gán môn học">
        <Row>
          <Col span={24}>
            <MenuTabTop />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <FormInfoCommon />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableRoomsForAssignSubject />
          </Col>
        </Row>
        <Row style={{ textAlign: 'right' }}>
          <Col span={24}>
            <div className="btn-group">
              <BtnSearch />
              <BtnAddNew />
              <BtnDelete />
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <TableSubjectNameNotSelected />
          </Col>
          <Col span={12}>
            <TableSubjectNameSelected />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ClassListPage />
  </Page>
);



